@import "../main.scss";

.navbar {
  position: absolute;
  width: 100%;
  height: 15vh;
  background-color: transparent;

}

.logo {
  position: absolute;
  width: 540px;
  height: 120px;
  top: 16px;
  left: 22px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: left;
  mix-blend-mode: multiply;


}

.buttons{
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  width: 250px;
  height: 40px;
  top: 25px;
  right: 70px;
  mix-blend-mode: multiply;



}

.dot{
  position: relative;
  width: 2px;
  height: 2px;
  background-color: black;
}


.link{
  text-decoration: none;
  color: black;
}


